<template>
  <div>
    <h2 class="page-heading"><span>{{ $t('nav.LOG IN') }}</span></h2>
    <b-form class="form1" @submit="onSubmit" @reset="onReset" v-if="show">
      <b-form-group
        id="input-group-1"
        label-for="input-1"
      >
      {{$t('login.email')}}
        <b-form-input
          id="input-1"
          v-model="form.email"
          type="email"
          required
          placeholder="Enter email"
        ></b-form-input>
      </b-form-group>

      <b-form-group id="input-group-2"  label-for="input-2">  {{$t('login.username')}}
        <b-form-input
          id="input-2"
          v-model="form.name"
          required
          placeholder="Enter User name"
        ></b-form-input>
      </b-form-group>

      <b-button class="button1" type="submit" variant="warning"> {{$t('login.submit')}}</b-button>
      <b-button class="button1" type="reset" variant="warning"> {{$t('login.reset')}}</b-button>
    </b-form>
    
  </div>
</template>

<script>
  export default {
    data() {
      return {
        form: {
          email: '',
          name: '',
          food: null,
          checked: []
        },
        foods: [{ text: 'Select One', value: null }, 'Carrots', 'Beans', 'Tomatoes', 'Corn'],
        show: true
      }
    },
    methods: {
      onSubmit(evt) {
        evt.preventDefault()
        alert(JSON.stringify(this.form))
      },
      onReset(evt) {
        evt.preventDefault()
        // Reset our form values
        this.form.email = ''
        this.form.name = ''
        this.form.food = null
        this.form.checked = []
        // Trick to reset/clear native browser form validation state
        this.show = false
        this.$nextTick(() => {
          this.show = true
        })
      }
    }
  }
</script>
